














export default {
	data() {
		return {
			mobile: '',
			password: '',
			password2: ''
		};
	},
	onLoad() {
	},
	onLoad: function(option) {
		//option为object类型，会序列化上个页面传递的参数
		console.log(option.mobile);
		this.mobile = option.mobile;
	},
	methods: {
		goBack:function(){
			uni.navigateTo({
				url:'../signin/signin'
			})
		},
		changepassword: function() {
			var _this = this;
			if (_this.password === _this.password2) {
				uni.request({
					url: this.apiServer + '/user/changepsd',
					method: 'POST',
					header: { 'content-type': 'application/x-www-form-urlencoded' },
					data: {
						mobile: _this.mobile,
						password: _this.password
					},
					success: res => {
						uni.showModal({
							title: '提示',
							content: '修改成功'
						});
						uni.navigateTo({
							url: '../signin/signin'
						});
					}
				});
			} else {
				uni.showModal({
					title: '提示',
					content: '密码不同'
				});
			}
		}
	}
};
